button {
  background: #fff;
  border-radius: 0;
  display: inline-block;
  cursor: pointer;
  -webkit-appearance: none;
  font-size: 1rem;
  padding: 0.5rem 1rem;
  border: 1px solid;
  font-family:inherit;
  -webkit-box-shadow: 1px 1px;
          box-shadow: 1px 1px;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
}
button:hover {
    -webkit-transform: translate(1px, 1px);
        -ms-transform: translate(1px, 1px);
            transform: translate(1px, 1px);
    -webkit-box-shadow: 0px 0px;
            box-shadow: 0px 0px;
    -webkit-transition: .2s;
    -o-transition: .2s;
    transition: .2s;
    background: #fffee8;
}
button.button,
.button[role="button"],
input.button[type="submit"] {
  background-color: #000;
  color: white;
  padding: 0.5em 1.5em;
}



